<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-left">
        <div class="c-l-t">
          <ctitle :title="'整体化解率'"></ctitle>
          <div class="inner">
            <cir-circle :count="hjvValue"></cir-circle>
          </div>
        </div>
        <div class="c-l-b">
          <ctitle :title="'信访件总量、办结、化解趋势'"></ctitle>
          <div class="charts">
            <LinesR :params="qsLineParams"></LinesR>
          </div>
        </div>
      </div>
      <div class="c-right">
        <div class="c-r-t">
          <ctitle :title="'国家交办、督办矛盾化解信访件'"></ctitle>
          <div class="inner">
            <div class="detail">
              <allCount class="all-count" :count="allcount"></allCount>
              <div class="inner" v-loading="!hjType.length"
                   :element-loading-background="$STANDARD.loadingBgColor">
                <div v-for="(item, index) in hjType" :key="index" class="detail-item">
                  <label class="name">{{item.name}}</label>
                  <incNumber class="number" :incNumber="item.jc"></incNumber>
                  <span class="unit">件次</span>
                  <incNumber class="per-number" :incNumber="item.jcPer"></incNumber>
                  <span class="per-unit">%</span>
                </div>
              </div>
            </div>

            <div class="chart" v-for="(item,index) of jbdbArr" :key="index">
              <pie-chart3D :params="item.params"></pie-chart3D>
              <p class="label">{{item.name}}</p>
            </div>
          </div>
        </div>
        <div class="c-r-b">
          <ctitle :title="'省、市、县登记矛盾化解信访件'"></ctitle>
          <div class="inner">
            <div class="chart" v-for="(item,index) of hjArr" :key="index">
              <loopPie :params="item.params"></loopPie>
              <p class="label">{{item.name}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import incNumber from "@/components/incNumber/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import loopPie from "@/components/pie/LoopPie";
  import LinesR from "@/components/line/LinesR";
  import cirCircle from "@/components/cirCircle/index";
  import allCount from "@/components/list/allCount";

  export default {
    name: "mdgjDjqy",
    components: {
      ctitle,
      incNumber,
      PieChart3D,
      loopPie,
      LinesR,
      cirCircle,
      allCount
    },
    data() {
      return {
        loadFlag: false,
        depList: [],
        // 1化解率
        hjvValue: -1,
        // 2.化解趋势
        qsLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "10%",
            top: "20%",
            bottom: "5%",
            legendTop: "5%",
            legendRight: "10%",
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        },
        // 3化解信访件（总数）
        allcount: -1,
        //4化解信访件（各类型）
        hjType: [],
        //5.交办督办化解信访件（饼图）
        jbdbArr: [
          {
            name: '交办',
            params: {
              data: [],
              styleObj: {
                width: "100%",
                height: "90%"
              }
            }
          },
          {
            name: '督办',
            params: {
              data: [],
              styleObj: {
                width: "100%",
                height: "90%"
              }
            }
          }
        ],
        jbdbPieParams: {
          data: [],
          angle: 20,
          radius: "30%",
          labelRadius: 15,
          fontSize: this.fontSize(0.16), // label字号
          color: ["#5166c1", "#32b1e0", "#15bbc2"],
          styleObj: {
            width: "100%",
            height: "90%"
          }
        },
        // 6.省市县化解信访件
        hjArr: [
          {name: '省级', params: {data: []}},
          {name: '市级', params: {data: []}},
          {name: '县级', params: {data: []}}
        ],
        hjPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: [
              ["#ffa8c5", "#5850ff"],
              ["#ff957c", "#ffe97d"],
              ["#dfffb1", "#79cc7a"],
            ],
            radius: ["55%", "70%"],
            center: ["50%", "50%"],
            centerRadius: ["42%", "43%"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            borderWidth: 5,
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        }
      };
    },
    mounted() {
      this.getAPI(this.year);
    },
    methods: {
      getAPI(year) {
        // 1.整体化解率
        get_http("gjzlSdgjDjjgHjvValue", {year: year})
          .then(res => {
            this.hjvValue = (res.data.data[0].value * 100).toFixed(0);
          })
          .catch(err => {
            console.log(err);
          });
        // 2.化解趋势
        get_http("gjzlSdgjDjjgQsLine", {year: year})
          .then(res => {
            this.qsLineParams.map = res.data.map;
            this.qsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.化解信访件（总数）
        get_http("gjzlSdgjDjjgHjtotal", {year: year})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.化解信访件（各类型）
        get_http("gjzlSdgjDjjgHjType", {year: year})
          .then(res => {
            this.hjType = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.交办督办化解信访件（饼图）（区分 djjg=交办 督办）
        this.getJbdbPieAPI(year);
        // 6.省市县化解信访件（饼图）（区分 djjg=省级 市级 县级）
        this.getHjPieAPI(year);
      },
      // 5.交办督办化解信访件（饼图）（区分 djjg=交办 督办）
      getJbdbPieAPI(year) {
        for (let item of this.jbdbArr.values()) {
          item.params = this.jbdbPieParams;
          get_http("gjzlSdgjDjjgJbdbHhjjPie", {year: year, djjg: item.name})
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      // 6.省市县化解信访件（饼图）（区分 djjg=省级 市级 县级）
      getHjPieAPI(year) {
        for (let item of this.hjArr.values()) {
          item.params = this.hjPieParams;
          get_http("gjzlSdgjDjjgSsxhjPie", {year: year, djjg: item.name})
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .c-left
        flex 2
        display flex
        flex-direction column
        border-right 1px #91e3f7 dashed

        .charts
          width 100%
          height 100%

        .c-l-t
          flex 1
          .inner
            width 100%
            height calc(100% - 0.28rem)
        .c-l-b
          flex 1

      .c-right
        flex 3
        display flex
        flex-direction column
        .inner
          display flex
          height calc(100% - 0.28rem)
          .chart
            flex 1
            .label
              color #fff
              font-weight bold
              font-size 0.2rem
              text-align center
        .c-r-t
          flex 1
          height 50%
          .label
            position relative
            top -30px
          .detail
            flex 1
            display flex
            flex-direction column
            align-items center
            justify-content center
            .all-count
              height 0.48rem
              padding .6rem 0 0.4rem 0
            .inner
              width 100%
              height 100%
              display flex
              flex-direction column
              .detail-item
                font-size 0
                line-height .4rem
                text-align center

                .name
                  font-size .16rem
                  color #fff
                  width: 1rem;
                  text-align right
                  margin-right 0.3rem
                  line-height 0.24rem

                .number
                  margin-right .3rem
                  color #13fbff
                  font-size .24rem
                  display inline-block

                .unit
                  color #13fbff
                  font-size .16rem
                  margin-right .4rem

                .per-number
                  color #ffde00
                  font-size .16rem

                .per-unit
                  color #ffde00
                  font-size .16rem

        .c-r-b
          flex 1
          height 50%
          .inner
            height calc(100% - 0.7rem)

</style>
